class MenuLeft extends React.Component {
  state = {
    collapsed: false,
  };

  toggleCollapsed = () => {
    this.setState({
      collapsed: !this.state.collapsed,
    });
  };

  render() {
    let props = this.props;
    return (
        <div style={{ width: 256 }}>
          <Button type="primary" onClick={this.toggleCollapsed} style={{ marginBottom: 16 }}>
            {React.createElement(this.state.collapsed ? MenuUnfoldOutlined : MenuFoldOutlined)}
          </Button>
          <Menu
              defaultSelectedKeys={['1']}
              defaultOpenKeys={['sub1']}
              mode="inline"
              theme="light"
              inlineCollapsed={this.state.collapsed}
          >
            <SubMenu key="sub1" icon={<CameraOutlined />} title="Портфолио">
              <Menu.Item key="1" onClick={props.handler}>Фотосеты</Menu.Item>
              <Menu.Item key="2" onClick={props.handler}>Пейзажи</Menu.Item>
              <Menu.Item key="3" onClick={props.handler}>18+</Menu.Item>
            </SubMenu>
            <SubMenu key="sub2" icon={<VideoCameraOutlined />} title="Видео">
              <Menu.Item key="4" onClick={props.handler}>Инстаграм для бизнеса</Menu.Item>
              <Menu.Item key="5" onClick={props.handler}>Художественное</Menu.Item>
              <SubMenu key="sub3" title="18+">
                <Menu.Item key="6" onClick={props.handler}>Эротика</Menu.Item>
                <Menu.Item key="7" onClick={props.handler}>Жесткое</Menu.Item>
              </SubMenu>
            </SubMenu>
            <Menu.Item key="8" icon={<BookOutlined />} onClick={props.handler}>
              Типография
            </Menu.Item>
            <Menu.Item key="9" icon={<CommentOutlined />} onClick={props.handler}>
              Отзывы
            </Menu.Item>
            <Menu.Item key="10" icon={<EnvironmentOutlined />} onClick={props.handler}>
              Контакты
            </Menu.Item>
          </Menu>
        </div>
    );
  }
}

